import { useEffect, useState } from "react";
import {
  useLmsConfigMaster,
  useLmsUpdateHoliday,
} from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import style from "../../FinanceManager/FinanceManager.module.css";
import CreateButton from "../../../Ui/Button/CreateButton";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
import { lms_boolean } from "../../../Constant/Lms/lms_constant";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { slashDateFormat } from "../../../Utils/Functions/dateUtility";

const LmsHoliday = ({ instituteId, lcid }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    mark_sunday: "",
    mark_saturday: "",
    // date_arr: [],
    date_arr: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [lmsUpdateHoliday] = useLmsUpdateHoliday();

  const { lmsConfigMaster, lmsConfigMasterRefetch } = useLmsConfigMaster({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });

  useEffect(() => {
    if (lmsConfigMaster?.holiday_config) {
      setState({
        mark_sunday: lmsConfigMaster?.holiday_config?.mark_sunday?.status ?? "",
        mark_saturday:
          lmsConfigMaster?.holiday_config?.mark_saturday?.status ?? "",
        // date_arr: [],
        date_arr: "",
        // date_arr: lmsConfigMaster?.holiday_config?.dDate ?? [],
      });
    }
  }, [lmsConfigMaster?.holiday_config]);

  const onSave = () => {
    if (lcid) {
      setDisabled((pre) => !pre);
      lmsUpdateHoliday({
        lcid: lcid,
        holiday: {
          mark_sunday: { status: state.mark_sunday },
          mark_saturday: { status: state.mark_saturday },
          date_arr: state.date_arr ? [state.date_arr] : [],
        },
      })
        .then(() => {
          lmsConfigMasterRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onSundayTile = (val) => {
    setState((prev) => ({
      ...prev,
      mark_sunday: val === "Yes" ? "ALL_SUNDAYS" : "",
    }));
  };
  const onSaturdayTile = (val) => {
    setState((prev) => ({
      ...prev,
      mark_saturday: val === "Yes" ? "ALL_SATURDAYS" : "",
    }));
  };
  const onDateGrab = (val) => {
    setState((prev) => ({
      ...prev,
      date_arr: slashDateFormat(val),
    }));
  };

  return (
    <>
      <div className={style.fm_header_container}>
        <h6></h6>
        <CreateButton
          label={t("update")}
          onAction={onSave}
          customStyle={{
            margin: "0",
            padding: "0.3rem 1rem",
          }}
        />
      </div>
      <BorderBottom />

      <div className={style.lms_rule_container}>
        <TileWrapper
          label="all_sundays"
          customTileStyle={{
            width: "20%",
          }}
        >
          {lms_boolean?.map((tile) => (
            <TileCard
              key={tile?.key}
              tile={tile}
              onTile={onSundayTile}
              activeTile={state.mark_sunday === "ALL_SUNDAYS" ? "Yes" : "No"}
            />
          ))}
        </TileWrapper>
        <TileWrapper
          label="all_saturdays"
          customTileStyle={{
            width: "20%",
          }}
        >
          {lms_boolean?.map((tile) => (
            <TileCard
              key={tile?.key}
              tile={tile}
              onTile={onSaturdayTile}
              activeTile={
                state.mark_saturday === "ALL_SATURDAYS" ? "Yes" : "No"
              }
            />
          ))}
        </TileWrapper>
        <FormCalendarInput
          label={t("select_leave_holiday")}
          placeholder={t("select_date")}
          name="calendar"
          value={state.date_arr}
          onChange={onDateGrab}
          customFormInputContainer={{
            width: "56%",
          }}
        />
      </div>
      <BorderBottom />

      <div className={style.fm_header_container} style={{}}>
        <h6>{t("holiday_leave_date")}</h6>
      </div>
      <div className={style.lms_rule_container}>
        {lmsConfigMaster?.holiday_config?.dDate?.map((dt) => (
          <p>{dt}</p>
        ))}
      </div>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default LmsHoliday;
